.level51-ajaxSelectFieldBase {
  position: relative;
}
.level51-ajaxSelectFieldBase input[type=text] {
  display: block;
  width: 100%;
  height: 40px;
  padding: 8px;
  border: 1px solid #ced5e1;
  background: #ffffff;
  transition: all 250ms ease-in-out;
  outline: none;
  box-shadow: none;
  -webkit-appearance: none;
  border-radius: 0.23rem;
}
.level51-ajaxSelectFieldBase input[type=text]:focus {
  border-color: #66afe9;
}
.level51-ajaxSelectFieldBase .vue-simple-suggest {
  position: relative;
}
.level51-ajaxSelectFieldBase .vue-simple-suggest > ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}
.level51-ajaxSelectFieldBase .vue-simple-suggest .suggestions {
  position: absolute;
  width: 100%;
  left: 0;
  top: 100%;
  background: rgba(255, 255, 255, 0.93);
  z-index: 1000;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.16);
  border: 1px solid #66afe9;
  border-top: 0;
  border-bottom-left-radius: 0.23rem;
  border-bottom-right-radius: 0.23rem;
  max-height: 360px;
  overflow-y: auto;
}
.level51-ajaxSelectFieldBase .vue-simple-suggest .suggestions .suggest-item {
  cursor: pointer;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.level51-ajaxSelectFieldBase .vue-simple-suggest .suggestions .suggest-item.hover,
.level51-ajaxSelectFieldBase .vue-simple-suggest .suggestions .suggest-item.selected {
  background-color: #66afe9;
  color: #ffffff;
}
.level51-ajaxSelectFieldBase .vue-simple-suggest .suggestions .suggest-item,
.level51-ajaxSelectFieldBase .vue-simple-suggest .suggestions .misc-item {
  padding: 8px;
}
